







































































import Vue from "vue";
import toggleRow from "./toggle-row.vue";
import { allDBObj } from "../modules/format-db";

export default Vue.extend({
  components: { toggleRow },
  props: {
    project: {
      type: String,
      default: "",
    },
  },
  data: () => ({
    hasSlots: undefined,
    allDBObj,
  }),
  methods: {
    sentToProjector(src) {
      this.$store.commit("setProject", {
        value: src,
      });
      console.clear();
    },
  },
  mounted() {
    this.hasSlots = this.$slots.default?.length;
  },
});
