
























import Vue from "vue";

export default Vue.extend({
  inheritAttrs: false,
  props: {
    color: {
      type: String,
      default: "cobalt-blue",
    },
    value: {
      type: Number,
    },
    label: {
      type: String,
      default: undefined,
    },
  },
  data: () => ({
    val: 0,
  }),
  methods: {
    updateValue() {
      this.$emit("update-value", this.val);
    },
  },
  created() {
    this.val = this.value;
  },
});
