








import Vue from "vue";
import { mapGetters } from "vuex";

export default Vue.extend({
  computed: {
    ...mapGetters({
      alert: "getAlert",
    }),
  },
  methods: {
    closeAlert() {
      this.$store.commit("setValue", {
        name: "alert",
        value: {
          message: "",
          status: "",
        },
      });
    },
  },
});
