




























import Vue from "vue";

export default Vue.extend({
  props: {
    width: {
      type: Number,
      default: 800,
    },
    ctaCaption: {
      type: String,
      default: "Close Modal",
    },
  },
  data: () => ({
    modal: {},
  }),
  mounted() {
    this.modal = this.$store.getters.getModal;
  },
  methods: {
    closeModal() {
      this.modal.hidden = true;
    },
  },
});
