




























































import Vue from "vue";

export default Vue.extend({
  data: () => ({
    selection: {},
  }),
  props: {
    cx: {
      type: Number,
      default: 0,
    },
    cy: {
      type: Number,
      default: 0,
    },
    hue: {
      type: Number,
      default: 0,
    },
  },
  computed: {
    delta(){
      return this.cy + this.cx;
    }
  }
});
