















import Vue from "vue";
import { allColors } from "../db/wiki-colors";

export default Vue.extend({
  data: () => ({
    gColorsDB: Object.values(allColors)
      .sort((a, b) => {
        return a.lightness - b.lightness;
      })
      .sort((a, b) => {
        return b.saturation - a.saturation;
      })
      .sort((a, b) => {
        return a.hue - b.hue;
      })
      .sort((a, b) => {
        return a.opacity - b.opacity;
      }),
  }),
});
